* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #232831;
  color: white;
  font-family: "Courier";
  overflow-x: hidden;
}

.row {
  display:flex;
  flex-flow: row nowrap;
}

.column {
  display: flex;
  flex-flow: column nowrap;
}

.navBarBackground {
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 60px;
  top: 0;
  left: 0;
  justify-content: flex-end;
  align-items: center;
  background-color: #F15455;
  background: linear-gradient(to bottom, #232831FF 50%, #23283100 90%);
  padding: 10px 0px 0px 0px;
}

.navBarName {
  width: 60%;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  z-index: 3;
}

.navBarName img {
  height: 35px;
}

.navBarLogo {
  width: 17%;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: flex-start;
}

.navBarLogo h1 {
  font-size: 2.1rem;
  font-family: "3D Isometric";
  width: auto;
  margin: 4px 0px 0px 0px;
  padding: 5px 30px;
  outline: 2px solid white;
  outline-offset: -5px;
  border-radius: 20px 10px 20px 10px;
  background-color: #232831;
}

.navBarLogo h2 {
  font-size: 1.2rem;
  font-family: "Amne Sans";
  width: auto;
  margin: 10px 0px 0px 0px;
  padding: 5px 10px 0px 10px;
  background-color: #232831;
  border-radius: 20px 10px 20px 10px;

}

.navBarName .social {
  width: 71%;
  height: 100%;
  position: relative;
  top: 0;
  left: 29%;
  justify-content: flex-start;
  align-items: center;
}

.navBarName .social a {
  height: 60%;
  margin: 0px 6px;
  transition: all .2s ease-in-out;
}

.navBarName .social svg {
  height: 100%;
  fill: #d9d9d9;
}

.navBarName .social .githubLogo { fill: #d9d9d9; transition: all .2s ease-in-out }
.navBarName .social .indeedLogoBG { fill: transparent; transition: all .2s ease-in-out }
.navBarName .social .indeedLogoLetters { fill: #d9d9d9; transition: all .2s ease-in-out }
.navBarName .social .electionLogoLetters { fill: #d9d9d9; transition: all .2s ease-in-out }
.navBarName .social .electionLogoRed { fill: #d9d9d9; transition: all .2s ease-in-out }
.navBarName .social .electionLogoBlue { fill: #a9a9a9; transition: all .2s ease-in-out }

.navBarName .social svg:hover .githubLogo { fill: white; transition: all .2s ease-in-out }
.navBarName .social svg:hover .indeedLogoBG { fill: #069; transition: all .2s ease-in-out }
.navBarName .social svg:hover .indeedLogoLetters { fill: white; transition: all .2s ease-in-out }
.navBarName .social svg:hover .electionLogoLetters { fill: #d9d9d9; transition: all .2s ease-in-out }
.navBarName .social svg:hover .electionLogoRed { fill: #fa1a54; transition: all .2s ease-in-out }
.navBarName .social svg:hover .electionLogoBlue { fill: #3146ff; transition: all .2s ease-in-out }

.navBar {
  justify-content: space-evenly;
  align-items: center;
  width: 40%;
  border-radius: 20px 20px 0px 0%;
  position: relative;
  z-index: 3;
}

.navBar h2 {
  margin: 0;
  padding: 10px;
  width: 20%;
  text-align: center;
  z-index: 3;
  font-family: "Turpis";
}

.navSVG {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  z-index: 2;
}

.navSVG svg path {
  transition: all .2s ease-in-out;
}

a {
  text-decoration: none;
  color: white;
  width: auto;
}

.sectionTitleContainer {
  width: 90%;
  display:flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
}

.sectionTitle {
