    align-items: center;
    position: relative;
}

.projectPanelSelector hr {
    border: 3px solid white;
    border-radius: 999px;
    width: 45%;
    transition: all .2s ease-in-out;
    margin: 10px;
    opacity: .3;
}

.projectPanelSelector .selected {
    opacity: 1;
}

